import { Axios } from 'axios'
import {
  Transaction,
  TransactionData,
  TransactionRoutes,
  TransactionsClientModel,
  TransactionsSearchQuery,
} from '../transaction.model'
import { ApiEntity } from '../../core'

export class TransactionsClient
  extends ApiEntity
  implements TransactionsClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Create a vault epoch
   * @param body - the vault epoch data
   * @returns the promise for create a new vault epoch
   */
  public create(body: TransactionData) {
    return this._create<Transaction>(TransactionRoutes.v1Create, body)
  }

  /**
   * Search epochs by params
   * @param searchParams - the vault epochs search params
   * @returns the promise for search epochs
   */
  public search(searchParams?: TransactionsSearchQuery) {
    return this._search<Transaction>(TransactionRoutes.v1Search, searchParams)
  }

  /**
   * Search all epoch by params
   * @param searchParams - the vault
   * @returns
   */
  public searchAll(searchParams?: TransactionsSearchQuery) {
    return this._searchAll<Transaction>(
      TransactionRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List epochs by params
   * @param searchParams - the search vault epochs search params
   * @returns the promise for list epochs
   */
  public list(searchParams?: TransactionsSearchQuery) {
    return this._list<Transaction>(TransactionRoutes.v1Search, searchParams)
  }

  /**
   * List all epochs by params
   * @param searchParams - the search vault epochs search params
   * @returns the promise for list epochs
   */
  public listAll(searchParams?: TransactionsSearchQuery) {
    return this._listAll<Transaction>(TransactionRoutes.v1Search, searchParams)
  }

  /**
   * Find an epoch by params
   * @param searchParams - the search params
   * @returns the promise for find an epoch
   */
  public findOne(searchParams?: TransactionsSearchQuery) {
    return this._findOne<Transaction>(TransactionRoutes.v1Search, searchParams)
  }
}
